import React from "react";
import { BrowserRouter as Router } from "react-router-dom";

import { Logo, Button, Typography } from "components";

import css from "./style.module.scss";

const ErrorScreen = () => {
  const handleClick = () => {
    window.location.reload();
  };

  return (
    <Router>
      <div className={css.wrapper}>
        <div className={css.innerWrapper}>
          <div className={css.logoWrapper}>
            <Logo
              className={css.logo}
              logoVariant={"notFoundPasino"}
              to={{ pathname: "/" }}
            />
          </div>
          <Typography variant="h1" className={css.title}>
            Something went wrong
          </Typography>
          <Typography variant="h6" className={css.description}>
            You need to enable setting data to run this app.
          </Typography>
          <Typography variant="h6" className={css.link}>
            <a href="https://tips.tutorialhorizon.com/2016/08/18/allowenable-localstorage-in-chrome/">
              https://tips.tutorialhorizon.com/2016/08/18/allowenable-localstorage-in-chrome/
            </a>
          </Typography>

          <div className={css.buttonWrapper}>
            <Button className={css.button} onClick={handleClick}>
              RELOAD
            </Button>
          </div>
        </div>
      </div>
    </Router>
  );
};

export default ErrorScreen;
